import React from 'react';

import { Routes, Route } from 'react-router-dom';

import SideNav from './SideNavbar';
import Header from './Header';

import { ReactComponent as AssetLoader } from "./../../assets/images/loader.svg";

import './admin-portal.scss';

const Courses = React.lazy(() => import('./Courses'));
const Content = React.lazy(() => import('./Content'));
const BlogsAndNews = React.lazy(() => import('./BlogsAndNews'));
const Partners = React.lazy(() => import('./Partners'));
const Settings = React.lazy(() => import('./Settings'));

// const Settings = React.lazy(async () => {
//     await new Promise(resolve => setTimeout(resolve, 500000));
//     return import('./Settings');
// });

const isWaiting = () => {
    return (
        <div className="lazy-loader">
            <AssetLoader />
        </div>
    );
};

const toggleMenu = () => {
    let toggle = document.querySelector('.toggle');
    toggle.classList.toggle('active');
    let navigation = document.querySelector('.nav-wrapper');
    navigation.classList.toggle('active');
    let main = document.querySelector('.main-panel');
    main.classList.toggle('active');
};

const AdminPortal = () => {

    return (
        <div className="wrapper main-content">
            <SideNav />
            <div className="main-panel">
                <Header toggleMenu={toggleMenu} />
                <div className="content">
                    <React.Suspense fallback={isWaiting()}>
                        <Routes>
                            <Route path='/' element={<div>Admin-Portal</div>} />
                            <Route path='dashboard/*' element={<div>Dashboard</div>} />
                            <Route path='courses/*' element={<Courses />} />
                            <Route path="cms/*" element={<Content/>}/>
                            <Route path="blogs-n-news/*" element={<BlogsAndNews/>}/>
                            <Route path="partners/*" element={<Partners/>}/>
                            <Route path="banners-n-ads/*" element={<div className="container-fluid p-4">Module is Under Development</div>}/>
                            <Route path="settings" element={<Settings/>}/>
                        </Routes>
                    </React.Suspense>
                </div>
            </div>
        </div>
    );


};

export default AdminPortal;