import React, { useContext } from 'react';

import { useTranslation } from "react-i18next";
import WebContext from '../../../application/contexts/WebContext';

import { ReactComponent as AssetMenu } from './../../../assets/images/icons/menu-admin.svg';
import { ReactComponent as AssetSun } from './../../../assets/images/icons/theme-sun.svg';
import { ReactComponent as AssetMoon } from './../../../assets/images/icons/theme-moon.svg';
import { ReactComponent as AssetEnglish } from './../../../assets/images/icons/flag-us.svg';
import { ReactComponent as AssetBulgarian } from './../../../assets/images/icons/flag-bg.svg';

import './header.scss';
import { Link, useNavigate } from 'react-router-dom';

const Header = ({ toggleMenu }) => {
    const navigate = useNavigate();
    const webCtx = useContext(WebContext);
    const { t, i18n } = useTranslation();

    const switchTheme = () => {
        const previousTheme = webCtx.appTheme;
        const theme = (webCtx.appTheme === 'light-theme') ? 'dark-theme' : 'light-theme';
        document.body.classList.replace(previousTheme, theme);
        webCtx.switchTheme(theme);
    };

    const switchLanguage = (e) => {
        const lang = (webCtx.appLang === 'en') ? 'bg' : 'en';
        i18n.changeLanguage(lang);
        webCtx.switchLang(lang);
    };

    const handleLogout = () => {
        webCtx.logout();
        navigate("/");
    };

    return (
        <header className="px-4">
            <div className="toggle me-3 d-block d-lg-none" onClick={() => toggleMenu()} ><AssetMenu /></div>


            <div className={"header-actions ms-auto d-flex "}>
                <div className="ms-auto">
                    <button className="btn nodis-btn outline small me-2" onClick={switchLanguage} data-toggle="tooltip" data-placement="bottom" title="Tooltip on right">
                        {(webCtx.appLang === 'en') ? <AssetBulgarian /> : <AssetEnglish />}
                    </button>

                    <button className="btn nodis-btn outline small me-2" onClick={switchTheme}>
                        {(webCtx.appTheme === 'light-theme') ? <AssetMoon /> : <AssetSun />}
                    </button>
                </div>

                <div className="dropdown profile-dd">
                    <div className="dropdown-toggle " type="button" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">
                        {webCtx.loginUserName}
                    </div>
                    <ul className="dropdown-menu nodis-dd-menu" aria-labelledby="dropdownMenuButton1">
                        {/* <li><Link to="#" className="dropdown-item">Profile</Link></li> */}
                        <li><Link to="/" className="dropdown-item">User View</Link></li>
                        <li><Link to="/admin-portal/settings" className="dropdown-item">Settings</Link></li>
                        <li><span className="dropdown-item pointer" onClick={handleLogout}>Logout</span></li>
                    </ul>
                </div>
            </div>

        </header>
    );
};

export default Header;